    bottom: 0;
    right: 0;
    background-color: $primary-color;
    width: 80%;
    margin-bottom: -7px;
    -webkit-animation: slide 0.5s forwards;
    -webkit-animation-delay: 2s;
    animation: slide 0.5s forwards;
    animation-delay: 2s;

    a {
        display: block;
        text-align: center;
        color: white;
        width: 100%;

    }

